const possibleTimeFormats = [
  'YYYY/MM/DD HH:mm:ss',
  'YYYY-MM-DD HH:mm:ss',
  'MM/DD/YYYY HH:mm:ss',
  'MM-DD-YYYY HH:mm:ss',
  'YYYY-MM-DD\'T\'HH:mm:ssZ',
  'YYYY/MM/DD\'T\'HH:mm:ssZ',
  'ddd MMM DD YYYY HH:mm:ss'
]

export {
  possibleTimeFormats
}



